import { Routes } from '@angular/router';
import { BlogdetailComponent } from '../blogdetail/blogdetail.component';
import { IndexComponent } from './index.component';

export const routes: Routes = [
    {
        path: '',
        redirectTo: 'page/1',
        pathMatch: 'full'
    },
    {
        path: 'page/:page',
        component: IndexComponent,
        data: {
            state: 'page'
        },
    }
];



