import { trigger, animate, transition, style, group, animateChild, query, stagger } from '@angular/animations';

export const routerTransition = trigger('routerTransition', [
    transition('* <=> *', [
        query(':enter, :leave', style({ position: 'fixed', width: '78.1%' }), { optional: true }),
        query('.block', style({ opacity: 0 }), { optional: true }),
        group([  // block executes in parallel
            query(':enter', [
                style({ transform: 'translateX(100%)' }),
                animate('.7s cubic-bezier(0.215, 0.61, 0.355, 1)', style({ transform: 'translateX(0) ' }))
            ], { optional: true }),
            query(':leave', [
                style({ transform: 'translateX(0)' }),
                animate('0.7s cubic-bezier(0.215, 0.61, 0.355, 1)', style({ transform: 'translateX(-100%)' }))
            ], { optional: true }),
        ]),
        // stragger为动画交错执行
        query(':enter .block', stagger(400, [
            style({ transform: 'translateY(100px)' }),
            animate('1s cubic-bezier(0.215, 0.61, 0.355, 1)',
                style({ transform: 'translateY(0)', opacity: 1 })),
        ]), { optional: true }),
    ])
]);


