import { Component, OnInit,  ElementRef, AfterViewInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from './services/blog.service';
import { routerTransition } from './../animation/router.animation';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss'],
  animations: [ routerTransition ]
})
export class IndexComponent implements OnInit, AfterViewInit {
  @ViewChild('bloglist') bloglist: ElementRef;
  currentIndex: number;
  total: any;
  itemsPerPage: number;
  blog_contents: any = [];
  i: any;
  constructor(
    public activedRoute: ActivatedRoute,
    public router: Router,
    public blogService: BlogService,
    private elementRef: ElementRef
  ) {
    this.currentIndex = 1;
    this.itemsPerPage = 9;
  }
  ngOnInit() {
  }
  ngAfterViewInit() {
    // const listnodes = this.bloglist.nativeElement.children;
    // console.log(listnodes[3].style.animationDelay);
    // listnodes[3].style.animationDelay = '.5s';
    // setInterval(function() {
    //   Array.from(listnodes).forEach((val, index, array) => {
    //     console.log(val);
    //   });
    // }, 5000);
    this.activedRoute.params.subscribe(params => {
      // 这里可以从路由里面获取URL参数
      // console.log(params);
      this.getData(this.currentIndex);
    });
  }
  public modelChange(event: any): void {
    this.router.navigateByUrl('home/page/' + event);
  }
  // 分页 获取博客数据
  getData(page: number) {
    const offset = (this.currentIndex - 1) * this.itemsPerPage;
    const end = (this.currentIndex) * this.itemsPerPage;
    this.blogService.getBlogs(page).subscribe(res => {
      this.total = res['total'];
      this.blog_contents = res['data'].slice(offset, end > this.total ? this.total : end);
    });
  }
  TrackBy(index) {
    return index;
  }
}



